*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    color: black;
    font-style: italic;
    
    
}
body{
    background-image: url(./wp10154402-4k-weather-wallpapers.jpg);
    width: 100vw;
    height: 100vh;
    background-size:cover;

}

#weatherDiv{
    text-align: center;
    
   
}

#weatherDiv>h1{
   padding-top: 2%;
  
}
#weatherDiv>input{
    margin-top: 4%;
    height: 40px;
    width: 300px;
    font-size:medium;
    color: black;
    border-radius: 20px;
    border: none;
    padding-left: 20px;
    font-style: normal;
    
   
}
#weatherDiv>button{
    height: 40px;
    width: 100px;
    background-color: red;
    border-radius: 5px;
    font-weight: bold;
    margin: 20px;
    border-radius: 20px;
    border: none;
    
    
   
}
#weatherDiv>button:hover{
    background-color: rgb(132, 239, 26);
    transition: 1s;
}


/*******************ShowTemp******************/
#CardMainDiv{
    margin-top: 4%;
    /* border: 2px solid green; */
    display:flex;
    align-items: center;
    justify-content: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.CardSubDiv{
    /* border: 2px solid red; */
    display:flex;
    gap: 15px;
    
    
   
    
}
.card{
    /* border: 2px solid blue; */
    height: 300px;
    width: 250px;
    text-align: center;
    background-color: rgba(82, 198, 180, 0.598);
    border-radius: 15px;
    box-shadow: 0 0 15px  black ;
    padding-top: 30%;
    
    
  
}
.card>h3{
    font-size: x-large;
    font-family: Arial, Helvetica, sans-serif;
}
.card>p{
    font-size: x-large;
}
#h5H{
    padding-bottom: 15px;
}
#pH{
    padding-top: 15px;
}

#footer{    
    padding-top: 50px;
    float:right;
    padding-right: 40px;
    
}
#footer>h3{
    color: chartreuse;
}
#footer>h4{
    padding-left: 60px;
    color: pink;
}
#footer>p{
    color: white;
}
#footer>p>a{
    color: blue;
    text-decoration: none;
}
